@import "../../../Tools/General.scss";

.customBg {
  opacity: 0.75;
  max-height: 100vh;
  background-color: $main;
}

.parallaxContainer {
  display: flex;


  color: #fff;

  text-align: center;

  height: 100vh;
  padding: 3rem;

  .parallaxText {
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    h2 {
      margin-bottom: 0.5em;
      font-size: 28px;
    }

    p {
      font-size: 16px;
    }
  }
}

@media screen and (min-width: 800px) {
  .customBg {
    opacity: 0.75;
  }

  .parallaxContainer {
    color: #fff;

  
    margin-left: 10vh;

    padding: 20px;

    .parallaxText {
      text-align: left;
      justify-content: end;
      padding-bottom: 10vh;

      width: 40%;
      h2 {
        margin-bottom: 0.5em;
        font-size: 48px;
      }

      p {
        font-size: 24px;
      }
    }
  }
}
