@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;1,400;1,500;1,600&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,700;0,800;1,200;1,300;1,400&display=swap");

  .carouselDiv {

  
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
  
    .containercity {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin: 0 40px;
      max-width: 100vw;
      height: 600px;
      gap: 2rem;
    }
    
    .titleContainer {
      font-size: 26px;
      text-align: center;
    
      margin-top: 40px;
      font-family: "Source Sans Pro", sans-serif;
    }
  }

  @media screen and (min-width: 800px) {
    .carouselDiv {

      height: 100vh;
      justify-content: end;
    
      .containercity {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin: 0 40px;
        max-width: 100vw;
        height: 600px;
        gap: 2rem;
        margin-bottom: 2.5rem;
      }
      
      .titleContainer {
        font-size: 26px;
        text-align: center;
      
        margin-top: 40px;
        font-family: "Source Sans Pro", sans-serif;
      }
    }
  }


  .bgScroll {
    background-color: #F4EFEF;
    height: 100%;
    text-align: center;
  }

  .section_3 {

    
    background-image: url('./../../Img/arance_ddocu.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;


    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    color: #fff;
  }